import axios from 'axios'
import store from '@/store/index'
import { Message } from 'element-ui';
import { refreshToken } from "@/apis/api"

const service = axios.create({
  baseURL: window['config'].ZEUS_API_URL, // 所有的请求地址前缀部分
  // timeout: 60000, // 请求超时时间毫秒
  withCredentials: false, // 异步请求携带cookie
})

// 添加请求拦截器
service.interceptors.request.use(
  function(config) {
    config.headers = {
      ...config.headers,
      token: store.state.userInfo ? JSON.parse(store.state.userInfo).token : ''
    }
    return config
  },
  function(error) {
    console.log(error)
    return Promise.reject(error)
  }
)

// 添加响应拦截器
service.interceptors.response.use(
  function(response) {
    // 2xx 范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    // dataAxios 是 axios 返回数据中的 data
    const dataAxios = response.data
    if (!dataAxios.code) {
      return dataAxios
    } else if (dataAxios.code === 1000 || dataAxios.code === 430) { //430:登录页校验用户权限为无权限
      return dataAxios
    } else if (dataAxios.code === 400) {
      // Message.error(dataAxios.message)
      store.commit('setUserInfo', null)
      store.commit('setMemberId', null)
      store.commit('setIsAdminAuth', false)
      localStorage.removeItem('isAdminAuth')
      // store.commit('setActivityId', null)
      localStorage.removeItem('userInfo')
      // localStorage.removeItem('activityId')
      localStorage.removeItem('memberId')
      // window.localStorage.clear()
      // 如果是登录接口返回400 就返回数据到登录页面去处理
      if (window.location.hash.split('?')[0] === '#/login' || window.location.hash.split('?')[0] === '#/activity') {
        return dataAxios
      } else { // 如果是其他接口返回400 就重定向到登录有页面
        window.location.href = "/#/login?code="+window.localStorage.getItem('activityCode')
      }
    } else if (dataAxios.code === 429) { // 单点登录
      return dataAxios
    } else if(dataAxios.code === 999) { //刷新token
      refreshToken().then(res => {
        if(res && res.code === 1000) {
          let userInfo = JSON.parse(store.state.userInfo)
          userInfo = Object.assign(userInfo, {
            token: res.result.token
          })
          window.localStorage.setItem('userInfo', JSON.stringify(userInfo))
          this.$store.commit('setUserInfo', JSON.stringify(userInfo))
        }
      })
    } else if(dataAxios.code === 500) {

    } else {
      Message.error(dataAxios.message)
      // location.reload() 刷新页面
    }
  },
  function(error) {
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    console.log(error)
    return Promise.reject(error)
  }
)

export default service
