import httpRequest from '@/request/index'
import qs from 'qs'


// pc-账号密码登录
export function apiPostLogin(params) {
  return httpRequest.post('/auth/login', qs.stringify(params), {
    headers: { 'loginMode': '1' }
  })
}
// 手机短信验证码登录
export function apiPostLoginBysms(params) {
  return httpRequest.post('/auth/login', qs.stringify(params), {
    headers: { 'loginMode': '2' }
  })
}
// token刷新 
export function refreshToken() {
  return httpRequest.post('/activity-business/token/refresh')
}
// 获取验证码
export function apiGetCaptcha() {
  return httpRequest.get('/activity-business/login/captcha')
}
// 获取手机短信验证码-用于报名参加活动
export function apiGetSms(params) {
  return httpRequest.get('/activity-business/login/sms', {params})
}
// 使用手机号验证用户是否有进入活动的权限
export function apiGetAuthEnter(params) {
  return httpRequest.get('/activity-business/c/check/phone', {params})
}
// 获取memberId
export function postMemberId(params) {
  return httpRequest.post('activity-business/c/memberId', params)
}
// larksr进入应用，传给后台taskId
export function sendTaskId(params) {
  return httpRequest.post('activity-business/c/statistic/connection', params)
}
// 校验报名状态
